document.addEventListener('DOMContentLoaded', function() {

    eventListeners();

    darkMode();
    eliminarmensaje();
    //console.log(obtenerCookieDark());
    DarkModeCookie(obtenerCookieDark());
});

function darkMode() {
    
    const prefiereDarkMode = window.matchMedia('(prefers-color-scheme: dark)');

    // console.log(prefiereDarkMode.matches);

    if(prefiereDarkMode.matches) {
        document.body.classList.add('dark-mode');
    } else {
        document.body.classList.remove('dark-mode');
    }

    prefiereDarkMode.addEventListener('change', function() {
        if(prefiereDarkMode.matches) {
            document.body.classList.add('dark-mode');
        } else {
            document.body.classList.remove('dark-mode');
        }
    });

    const botonDarkMode = document.querySelector('.dark-mode-boton');
    botonDarkMode.addEventListener('click', function() {
        //document.body.classList.toggle('dark-mode'); 
        //Con cookies
        cookvalor= obtenerCookieDark();  
        if (cookvalor=='off') {
            document.cookie = "dark-mode=on"; 
        }else {
            document.cookie = "dark-mode=off"; 
        }
        //console.log(obtenerCookieDark());
        DarkModeCookie(obtenerCookieDark());
        
    });
}

function eventListeners() {
    const mobileMenu = document.querySelector('.mobile-menu');
    mobileMenu.addEventListener('click', navegacionResponsive);
    //muestra campos condicionales

    const metodocontacto =document.querySelectorAll('input[name="contacto[contacto]"]');
    metodocontacto.forEach(element => {
        element.addEventListener('click',mostrarc)
       // console.log(element)
    }
    );
}

function mostrarc(e) {
    const contactodiv=document.querySelector('#contacto');
    if (e.target.value=='telefono') {
        contactodiv.innerHTML=`
        <label for="telefono">Numero de Teléfono</label>
        <input data-cy='input-telefono' required name="contacto[telefono]" type="tel" placeholder="Tu Teléfono" id="telefono">

        <p>Si eligió teléfono, elija la fecha y la hora</p>

        <label for="fecha">Fecha:</label>
        <input data-cy='input-fecha' required name="contacto[fecha]" type="date" id="fecha">
        <label for="hora">Hora:</label>
        <input data-cy='input-hora' required name="contacto[hora]" type="time" id="hora" min="09:00" max="18:00">
        `;  
    } else {
        contactodiv.innerHTML=``;
    }
}


function navegacionResponsive() {
    const navegacion = document.querySelector('.navegacion');

    navegacion.classList.toggle('mostrar')
}

function obtenerCookieDark(){
    //intento por cookies
    //document.cookie = "dark-mode=on";
    //document.cookie = "dark-mode=on;expires=1 Mar 1990 00:00:00 GMT";
    listaCookies=document.cookie.split(";");
    micookie='';
    for (i in listaCookies) {
        resultado = listaCookies[i].search("dark-mode");
        if (resultado >-1) {
            micookie=listaCookies[i];
        }
        }
    if(micookie){
        igual=micookie.indexOf("=");
        valor = micookie.substring(igual+1);
    }else{
        valor='off';
    }
    //console.log(valor);
    return valor;
}

function DarkModeCookie(valor){
    if(valor=='on') {
        document.body.classList.add('dark-mode');
    } else {
        document.body.classList.remove('dark-mode');
    }
}

function eliminarmensaje() {
    if (document.body.contains(document.getElementById('mensajelogin'))) {
        var div = document.getElementById('mensajelogin');
        setTimeout(() => {
        div.classList.add('desaparecer');
        }, 5000);
        //console.log('si lo contiene');
        return;
    } 
    //console.log('no lo contiene');    
}